'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { DialerSettings, VoIPProvider, DialerMode, AMDAction, DialerDisposition } from '@/lib/power-dialer/types'

// ==========================================
// POWER DIALER SETTINGS PAGE
// VoIP Configuration, Dialer Settings, Dispositions
// ==========================================

export default function DialerSettingsPage() {
  const [activeTab, setActiveTab] = useState<'provider' | 'dialer' | 'dispositions' | 'compliance'>('provider')
  const [settings, setSettings] = useState<DialerSettings | null>(null)
  const [dispositions, setDispositions] = useState<DialerDisposition[]>([])
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [message, setMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null)

  // TODO: Get from auth context
  const subAccountId = 'b0000000-0000-0000-0000-000000000001'

  useEffect(() => {
    fetchSettings()
    fetchDispositions()
  }, [])

  const fetchSettings = async () => {
    setLoading(true)
    try {
      const res = await fetch(`/api/v1/dialer/settings?subAccountId=${subAccountId}`)
      const data = await res.json()
      if (data.success && data.data) {
        setSettings(data.data)
      } else {
        // Initialize with defaults
        setSettings(getDefaultSettings())
      }
    } catch (err) {
      console.error('Error fetching settings:', err)
      setSettings(getDefaultSettings())
    } finally {
      setLoading(false)
    }
  }

  const fetchDispositions = async () => {
    try {
      const res = await fetch(`/api/v1/dialer/dispositions?subAccountId=${subAccountId}&activeOnly=false`)
      const data = await res.json()
      if (data.success) {
        setDispositions(data.data)
      }
    } catch (err) {
      console.error('Error fetching dispositions:', err)
    }
  }

  const getDefaultSettings = (): DialerSettings => ({
    id: '',
    subAccountId,
    provider: 'twilio',
    outboundCallerIds: [],
    dialerMode: 'preview',
    maxConcurrentCalls: 1,
    callTimeout: 30,
    wrapUpTime: 30,
    autoDialDelay: 3,
    recordCalls: true,
    recordingRetentionDays: 90,
    amdEnabled: true,
    amdTimeout: 5,
    amdAction: 'drop_voicemail',
    workingHoursEnabled: true,
    workingHoursStart: '09:00',
    workingHoursEnd: '17:00',
    workingDays: [1, 2, 3, 4, 5],
    timezone: 'America/New_York',
    dncListEnabled: true,
    maxCallAttemptsPerDay: 3,
    maxCallAttemptsTotal: 10,
    minTimeBetweenAttempts: 60,
    aiTranscriptionEnabled: false,
    aiSentimentAnalysis: false,
    aiCallSummary: false,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString()
  })

  const saveSettings = async () => {
    if (!settings) return

    setSaving(true)
    setMessage(null)

    try {
      const method = settings.id ? 'PUT' : 'POST'
      const res = await fetch('/api/v1/dialer/settings', {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          ...settings,
          subAccountId
        })
      })

      const data = await res.json()
      if (data.success) {
        setSettings(data.data)
        setMessage({ type: 'success', text: 'Settings saved successfully!' })
      } else {
        setMessage({ type: 'error', text: data.error || 'Failed to save settings' })
      }
    } catch (err) {
      setMessage({ type: 'error', text: 'Network error' })
    } finally {
      setSaving(false)
    }
  }

  if (loading) {
    return (
      <div className="p-6 max-w-4xl mx-auto">
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
        </div>
      </div>
    )
  }

  return (
    <div className="p-6 max-w-4xl mx-auto">
      {/* Header */}
      <div className="flex justify-between items-center mb-6">
        <div>
          <div className="flex items-center gap-3">
            <Link href="/sub-account/power-dialer" className="text-gray-400 hover:text-gray-600">
              <i className="fas fa-arrow-left"></i>
            </Link>
            <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
              <i className="fas fa-cog text-orange-600"></i>
              Dialer Settings
            </h1>
          </div>
          <p className="text-gray-500 text-sm mt-1 ml-8">
            Configure your power dialer and VoIP settings
          </p>
        </div>
        <button
          onClick={saveSettings}
          disabled={saving}
          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 flex items-center gap-2"
        >
          {saving ? (
            <>
              <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
              Saving...
            </>
          ) : (
            <>
              <i className="fas fa-save"></i>
              Save Settings
            </>
          )}
        </button>
      </div>

      {/* Message */}
      {message && (
        <div className={`mb-6 p-4 rounded-lg ${
          message.type === 'success' ? 'bg-green-50 border border-green-200 text-green-700' : 'bg-red-50 border border-red-200 text-red-700'
        }`}>
          <i className={`fas ${message.type === 'success' ? 'fa-check-circle' : 'fa-exclamation-circle'} mr-2`}></i>
          {message.text}
        </div>
      )}

      {/* Tabs */}
      <div className="flex gap-1 mb-6 bg-gray-100 p-1 rounded-lg w-fit">
        {[
          { id: 'provider', label: 'VoIP Provider', icon: 'fas fa-phone' },
          { id: 'dialer', label: 'Dialer Settings', icon: 'fas fa-sliders-h' },
          { id: 'dispositions', label: 'Dispositions', icon: 'fas fa-tags' },
          { id: 'compliance', label: 'Compliance', icon: 'fas fa-shield-alt' }
        ].map(tab => (
          <button
            key={tab.id}
            onClick={() => setActiveTab(tab.id as typeof activeTab)}
            className={`px-4 py-2 rounded-md flex items-center gap-2 transition-colors ${
              activeTab === tab.id
                ? 'bg-white text-blue-600 shadow-sm'
                : 'text-gray-600 hover:text-gray-900'
            }`}
          >
            <i className={tab.icon}></i>
            {tab.label}
          </button>
        ))}
      </div>

      {/* Content */}
      <div className="bg-white rounded-xl shadow-sm border">
        {activeTab === 'provider' && settings && (
          <VoIPProviderSettings settings={settings} onChange={setSettings} />
        )}
        {activeTab === 'dialer' && settings && (
          <DialerModeSettings settings={settings} onChange={setSettings} />
        )}
        {activeTab === 'dispositions' && (
          <DispositionsSettings 
            dispositions={dispositions} 
            onUpdate={fetchDispositions}
            subAccountId={subAccountId}
          />
        )}
        {activeTab === 'compliance' && settings && (
          <ComplianceSettings settings={settings} onChange={setSettings} />
        )}
      </div>
    </div>
  )
}

// ==========================================
// VOIP PROVIDER SETTINGS
// ==========================================

function VoIPProviderSettings({
  settings,
  onChange
}: {
  settings: DialerSettings
  onChange: (settings: DialerSettings) => void
}) {
  const [showApiKey, setShowApiKey] = useState(false)
  const [newCallerId, setNewCallerId] = useState('')

  const providers: { id: VoIPProvider; name: string; icon: string }[] = [
    { id: 'twilio', name: 'Twilio', icon: 'ðŸ”µ' },
    { id: 'vonage', name: 'Vonage', icon: 'ðŸŸ£' },
    { id: 'plivo', name: 'Plivo', icon: 'ðŸŸ¢' },
    { id: 'custom', name: 'Custom/Other', icon: 'âš™ï¸' }
  ]

  const addCallerId = () => {
    if (newCallerId && !settings.outboundCallerIds.includes(newCallerId)) {
      onChange({
        ...settings,
        outboundCallerIds: [...settings.outboundCallerIds, newCallerId]
      })
      setNewCallerId('')
    }
  }

  const removeCallerId = (id: string) => {
    onChange({
      ...settings,
      outboundCallerIds: settings.outboundCallerIds.filter(c => c !== id)
    })
  }

  return (
    <div className="p-6 space-y-6">
      {/* Provider Selection */}
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-4">VoIP Provider</h3>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
          {providers.map(provider => (
            <button
              key={provider.id}
              onClick={() => onChange({ ...settings, provider: provider.id })}
              className={`p-4 rounded-lg border-2 text-center transition-all ${
                settings.provider === provider.id
                  ? 'border-blue-500 bg-blue-50'
                  : 'border-gray-200 hover:border-gray-300'
              }`}
            >
              <span className="text-2xl mb-2 block">{provider.icon}</span>
              <span className="font-medium text-gray-900">{provider.name}</span>
            </button>
          ))}
        </div>
      </div>

      {/* API Credentials */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">API Credentials</h3>
        <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 mb-4">
          <div className="flex items-start gap-3">
            <i className="fas fa-exclamation-triangle text-yellow-600 mt-1"></i>
            <div>
              <p className="font-medium text-yellow-800">Important</p>
              <p className="text-sm text-yellow-700">
                Your API credentials are encrypted and stored securely. Never share these credentials publicly.
              </p>
            </div>
          </div>
        </div>

        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Account SID
            </label>
            <input
              type="text"
              value={settings.accountSid || ''}
              onChange={(e) => onChange({ ...settings, accountSid: e.target.value })}
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
              placeholder="ACxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Auth Token
            </label>
            <div className="relative">
              <input
                type={showApiKey ? 'text' : 'password'}
                value={settings.authToken || ''}
                onChange={(e) => onChange({ ...settings, authToken: e.target.value })}
                className="w-full px-4 py-2 pr-12 border rounded-lg focus:ring-2 focus:ring-blue-500"
                placeholder="â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢"
              />
              <button
                type="button"
                onClick={() => setShowApiKey(!showApiKey)}
                className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600"
              >
                <i className={`fas ${showApiKey ? 'fa-eye-slash' : 'fa-eye'}`}></i>
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Outbound Caller IDs */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Outbound Caller IDs</h3>
        <p className="text-sm text-gray-500 mb-4">
          Add verified phone numbers to use as caller ID for outbound calls.
        </p>

        <div className="flex gap-2 mb-4">
          <input
            type="tel"
            value={newCallerId}
            onChange={(e) => setNewCallerId(e.target.value)}
            className="flex-1 px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
            placeholder="+1 (555) 123-4567"
          />
          <button
            onClick={addCallerId}
            className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
          >
            Add
          </button>
        </div>

        <div className="space-y-2">
          {settings.outboundCallerIds.map((callerId, index) => (
            <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
              <div className="flex items-center gap-3">
                <i className="fas fa-phone text-gray-400"></i>
                <span>{callerId}</span>
                {index === 0 && (
                  <span className="text-xs bg-blue-100 text-blue-700 px-2 py-0.5 rounded">Default</span>
                )}
              </div>
              <button
                onClick={() => removeCallerId(callerId)}
                className="text-red-500 hover:text-red-700"
              >
                <i className="fas fa-trash"></i>
              </button>
            </div>
          ))}
          {settings.outboundCallerIds.length === 0 && (
            <div className="text-center py-4 text-gray-400">
              No caller IDs added yet
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

// ==========================================
// DIALER MODE SETTINGS
// ==========================================

function DialerModeSettings({
  settings,
  onChange
}: {
  settings: DialerSettings
  onChange: (settings: DialerSettings) => void
}) {
  const dialerModes: { id: DialerMode; name: string; description: string }[] = [
    {
      id: 'preview',
      name: 'Preview Dialer',
      description: 'Agent reviews contact info before initiating the call. Best for quality conversations.'
    },
    {
      id: 'progressive',
      name: 'Progressive Dialer',
      description: 'Automatically dials the next number when agent becomes available. Balanced efficiency.'
    },
    {
      id: 'predictive',
      name: 'Predictive Dialer',
      description: 'Uses algorithms to dial multiple numbers simultaneously. Maximum efficiency.'
    }
  ]

  const amdActions: { id: AMDAction; name: string }[] = [
    { id: 'hangup', name: 'Hang up' },
    { id: 'drop_voicemail', name: 'Drop voicemail' },
    { id: 'continue', name: 'Continue to agent' }
  ]

  return (
    <div className="p-6 space-y-6">
      {/* Dialer Mode */}
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Dialer Mode</h3>
        <div className="space-y-3">
          {dialerModes.map(mode => (
            <label
              key={mode.id}
              className={`block p-4 rounded-lg border-2 cursor-pointer transition-all ${
                settings.dialerMode === mode.id
                  ? 'border-blue-500 bg-blue-50'
                  : 'border-gray-200 hover:border-gray-300'
              }`}
            >
              <div className="flex items-start gap-3">
                <input
                  type="radio"
                  name="dialerMode"
                  value={mode.id}
                  checked={settings.dialerMode === mode.id}
                  onChange={() => onChange({ ...settings, dialerMode: mode.id })}
                  className="mt-1"
                />
                <div>
                  <span className="font-medium text-gray-900">{mode.name}</span>
                  <p className="text-sm text-gray-500 mt-1">{mode.description}</p>
                </div>
              </div>
            </label>
          ))}
        </div>
      </div>

      {/* Call Settings */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Call Settings</h3>
        <div className="grid grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Call Timeout (seconds)
            </label>
            <input
              type="number"
              value={settings.callTimeout}
              onChange={(e) => onChange({ ...settings, callTimeout: parseInt(e.target.value) || 30 })}
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
              min="10"
              max="120"
            />
            <p className="text-xs text-gray-500 mt-1">Time to wait for answer</p>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Wrap-up Time (seconds)
            </label>
            <input
              type="number"
              value={settings.wrapUpTime}
              onChange={(e) => onChange({ ...settings, wrapUpTime: parseInt(e.target.value) || 30 })}
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
              min="0"
              max="300"
            />
            <p className="text-xs text-gray-500 mt-1">Time for disposition after call</p>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Auto-dial Delay (seconds)
            </label>
            <input
              type="number"
              value={settings.autoDialDelay}
              onChange={(e) => onChange({ ...settings, autoDialDelay: parseInt(e.target.value) || 3 })}
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
              min="0"
              max="30"
            />
            <p className="text-xs text-gray-500 mt-1">Delay before auto-dialing next</p>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Max Concurrent Calls
            </label>
            <input
              type="number"
              value={settings.maxConcurrentCalls}
              onChange={(e) => onChange({ ...settings, maxConcurrentCalls: parseInt(e.target.value) || 1 })}
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
              min="1"
              max="10"
            />
            <p className="text-xs text-gray-500 mt-1">For predictive mode</p>
          </div>
        </div>
      </div>

      {/* Recording Settings */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Recording Settings</h3>
        <div className="space-y-4">
          <label className="flex items-center gap-3">
            <input
              type="checkbox"
              checked={settings.recordCalls}
              onChange={(e) => onChange({ ...settings, recordCalls: e.target.checked })}
              className="w-5 h-5 rounded border-gray-300 text-blue-600"
            />
            <span className="text-gray-700">Record all calls</span>
          </label>

          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Recording Retention (days)
              </label>
              <input
                type="number"
                value={settings.recordingRetentionDays}
                onChange={(e) => onChange({ ...settings, recordingRetentionDays: parseInt(e.target.value) || 90 })}
                className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                min="7"
                max="365"
                disabled={!settings.recordCalls}
              />
            </div>
          </div>
        </div>
      </div>

      {/* AMD Settings */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Answering Machine Detection (AMD)</h3>
        <div className="space-y-4">
          <label className="flex items-center gap-3">
            <input
              type="checkbox"
              checked={settings.amdEnabled}
              onChange={(e) => onChange({ ...settings, amdEnabled: e.target.checked })}
              className="w-5 h-5 rounded border-gray-300 text-blue-600"
            />
            <span className="text-gray-700">Enable AMD</span>
          </label>

          {settings.amdEnabled && (
            <div className="grid grid-cols-2 gap-4 pl-8">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Detection Timeout (seconds)
                </label>
                <input
                  type="number"
                  value={settings.amdTimeout}
                  onChange={(e) => onChange({ ...settings, amdTimeout: parseInt(e.target.value) || 5 })}
                  className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                  min="1"
                  max="15"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  On Machine Detection
                </label>
                <select
                  value={settings.amdAction}
                  onChange={(e) => onChange({ ...settings, amdAction: e.target.value as AMDAction })}
                  className="w-full px-4 py-2 border rounded-lg bg-white"
                >
                  {amdActions.map(action => (
                    <option key={action.id} value={action.id}>{action.name}</option>
                  ))}
                </select>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* AI Features */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">AI Features</h3>
        <div className="space-y-4">
          <label className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
            <div>
              <span className="font-medium text-gray-900">Call Transcription</span>
              <p className="text-sm text-gray-500">Automatically transcribe recorded calls</p>
            </div>
            <input
              type="checkbox"
              checked={settings.aiTranscriptionEnabled}
              onChange={(e) => onChange({ ...settings, aiTranscriptionEnabled: e.target.checked })}
              className="w-5 h-5 rounded border-gray-300 text-blue-600"
            />
          </label>

          <label className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
            <div>
              <span className="font-medium text-gray-900">Sentiment Analysis</span>
              <p className="text-sm text-gray-500">Analyze call sentiment automatically</p>
            </div>
            <input
              type="checkbox"
              checked={settings.aiSentimentAnalysis}
              onChange={(e) => onChange({ ...settings, aiSentimentAnalysis: e.target.checked })}
              className="w-5 h-5 rounded border-gray-300 text-blue-600"
            />
          </label>

          <label className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
            <div>
              <span className="font-medium text-gray-900">Call Summary</span>
              <p className="text-sm text-gray-500">Generate AI summaries of calls</p>
            </div>
            <input
              type="checkbox"
              checked={settings.aiCallSummary}
              onChange={(e) => onChange({ ...settings, aiCallSummary: e.target.checked })}
              className="w-5 h-5 rounded border-gray-300 text-blue-600"
            />
          </label>
        </div>
      </div>
    </div>
  )
}

// ==========================================
// DISPOSITIONS SETTINGS
// ==========================================

function DispositionsSettings({
  dispositions,
  onUpdate,
  subAccountId
}: {
  dispositions: DialerDisposition[]
  onUpdate: () => void
  subAccountId: string
}) {
  const [showAddModal, setShowAddModal] = useState(false)

  const deleteDisposition = async (id: string) => {
    if (!confirm('Are you sure you want to delete this disposition?')) return

    try {
      const res = await fetch(`/api/v1/dialer/dispositions?id=${id}&subAccountId=${subAccountId}`, {
        method: 'DELETE'
      })
      if (res.ok) {
        onUpdate()
      }
    } catch (err) {
      console.error('Error deleting disposition:', err)
    }
  }

  const categoryColors = {
    positive: 'bg-green-100 text-green-700',
    neutral: 'bg-blue-100 text-blue-700',
    negative: 'bg-red-100 text-red-700',
    callback: 'bg-yellow-100 text-yellow-700'
  }

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-4">
        <div>
          <h3 className="text-lg font-semibold text-gray-900">Call Dispositions</h3>
          <p className="text-sm text-gray-500">Manage call outcome options for agents</p>
        </div>
        <button
          onClick={() => setShowAddModal(true)}
          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center gap-2"
        >
          <i className="fas fa-plus"></i>
          Add Disposition
        </button>
      </div>

      <div className="space-y-3">
        {dispositions.map(disp => (
          <div key={disp.id} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
            <div className="flex items-center gap-4">
              <div 
                className="w-10 h-10 rounded-lg flex items-center justify-center"
                style={{ backgroundColor: disp.color + '20', color: disp.color }}
              >
                <i className={disp.icon || 'fas fa-tag'}></i>
              </div>
              <div>
                <div className="flex items-center gap-2">
                  <span className="font-medium text-gray-900">{disp.name}</span>
                  <code className="text-xs bg-gray-200 px-2 py-0.5 rounded">{disp.code}</code>
                  <span className={`text-xs px-2 py-0.5 rounded ${categoryColors[disp.category]}`}>
                    {disp.category}
                  </span>
                  {disp.isSystem && (
                    <span className="text-xs bg-gray-300 text-gray-600 px-2 py-0.5 rounded">System</span>
                  )}
                </div>
                {disp.description && (
                  <p className="text-sm text-gray-500">{disp.description}</p>
                )}
                <div className="flex gap-3 mt-1 text-xs text-gray-400">
                  {disp.requiresNotes && <span><i className="fas fa-sticky-note mr-1"></i>Requires notes</span>}
                  {disp.requiresCallback && <span><i className="fas fa-calendar mr-1"></i>Requires callback</span>}
                  {disp.removesFromQueue && <span><i className="fas fa-check mr-1"></i>Removes from queue</span>}
                </div>
              </div>
            </div>
            <div className="flex items-center gap-3">
              <span className="text-sm text-gray-400">{disp.timesUsed} uses</span>
              {!disp.isSystem && (
                <button
                  onClick={() => deleteDisposition(disp.id)}
                  className="text-red-500 hover:text-red-700"
                >
                  <i className="fas fa-trash"></i>
                </button>
              )}
            </div>
          </div>
        ))}
        {dispositions.length === 0 && (
          <div className="text-center py-8 text-gray-400">
            <i className="fas fa-tags text-4xl mb-3 opacity-50"></i>
            <p>No dispositions configured</p>
          </div>
        )}
      </div>

      {/* Add Modal */}
      {showAddModal && (
        <AddDispositionModal
          onClose={() => setShowAddModal(false)}
          onAdded={() => {
            setShowAddModal(false)
            onUpdate()
          }}
          subAccountId={subAccountId}
        />
      )}
    </div>
  )
}

// ==========================================
// ADD DISPOSITION MODAL
// ==========================================

function AddDispositionModal({
  onClose,
  onAdded,
  subAccountId
}: {
  onClose: () => void
  onAdded: () => void
  subAccountId: string
}) {
  const [formData, setFormData] = useState({
    name: '',
    code: '',
    description: '',
    category: 'neutral' as 'positive' | 'negative' | 'neutral' | 'callback',
    color: '#3B82F6',
    icon: 'fas fa-tag',
    requiresNotes: false,
    requiresCallback: false,
    removesFromQueue: false
  })
  const [saving, setSaving] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!formData.name || !formData.code) return

    setSaving(true)
    try {
      const res = await fetch('/api/v1/dialer/dispositions', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          subAccountId,
          ...formData,
          isActive: true
        })
      })

      if (res.ok) {
        onAdded()
      }
    } catch (err) {
      console.error('Error adding disposition:', err)
    } finally {
      setSaving(false)
    }
  }

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      <div className="absolute inset-0 bg-black/50" onClick={onClose}></div>
      <div className="relative bg-white rounded-2xl shadow-xl w-full max-w-md mx-4">
        <div className="p-6">
          <h2 className="text-xl font-bold text-gray-900 mb-4">Add Disposition</h2>

          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Name *</label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  className="w-full px-4 py-2 border rounded-lg"
                  placeholder="Interested"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Code *</label>
                <input
                  type="text"
                  value={formData.code}
                  onChange={(e) => setFormData({ ...formData, code: e.target.value.toUpperCase() })}
                  className="w-full px-4 py-2 border rounded-lg font-mono"
                  placeholder="INT"
                  maxLength={10}
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Description</label>
              <input
                type="text"
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                className="w-full px-4 py-2 border rounded-lg"
                placeholder="Prospect showed interest"
              />
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Category</label>
                <select
                  value={formData.category}
                  onChange={(e) => setFormData({ ...formData, category: e.target.value as typeof formData.category })}
                  className="w-full px-4 py-2 border rounded-lg bg-white"
                >
                  <option value="positive">Positive</option>
                  <option value="neutral">Neutral</option>
                  <option value="negative">Negative</option>
                  <option value="callback">Callback</option>
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Color</label>
                <input
                  type="color"
                  value={formData.color}
                  onChange={(e) => setFormData({ ...formData, color: e.target.value })}
                  className="w-full h-10 border rounded-lg cursor-pointer"
                />
              </div>
            </div>

            <div className="space-y-3">
              <label className="flex items-center gap-2">
                <input
                  type="checkbox"
                  checked={formData.requiresNotes}
                  onChange={(e) => setFormData({ ...formData, requiresNotes: e.target.checked })}
                  className="rounded"
                />
                <span className="text-sm text-gray-700">Requires notes</span>
              </label>
              <label className="flex items-center gap-2">
                <input
                  type="checkbox"
                  checked={formData.requiresCallback}
                  onChange={(e) => setFormData({ ...formData, requiresCallback: e.target.checked })}
                  className="rounded"
                />
                <span className="text-sm text-gray-700">Requires callback scheduling</span>
              </label>
              <label className="flex items-center gap-2">
                <input
                  type="checkbox"
                  checked={formData.removesFromQueue}
                  onChange={(e) => setFormData({ ...formData, removesFromQueue: e.target.checked })}
                  className="rounded"
                />
                <span className="text-sm text-gray-700">Removes contact from queue</span>
              </label>
            </div>

            <div className="flex gap-3 pt-4">
              <button
                type="button"
                onClick={onClose}
                className="flex-1 px-4 py-2 border rounded-lg hover:bg-gray-50"
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={saving || !formData.name || !formData.code}
                className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50"
              >
                {saving ? 'Adding...' : 'Add Disposition'}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

// ==========================================
// COMPLIANCE SETTINGS
// ==========================================

function ComplianceSettings({
  settings,
  onChange
}: {
  settings: DialerSettings
  onChange: (settings: DialerSettings) => void
}) {
  const days = [
    { id: 0, name: 'Sun' },
    { id: 1, name: 'Mon' },
    { id: 2, name: 'Tue' },
    { id: 3, name: 'Wed' },
    { id: 4, name: 'Thu' },
    { id: 5, name: 'Fri' },
    { id: 6, name: 'Sat' }
  ]

  const toggleDay = (dayId: number) => {
    const current = settings.workingDays || []
    const updated = current.includes(dayId)
      ? current.filter(d => d !== dayId)
      : [...current, dayId].sort()
    onChange({ ...settings, workingDays: updated })
  }

  return (
    <div className="p-6 space-y-6">
      {/* Working Hours */}
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Working Hours</h3>
        <label className="flex items-center gap-3 mb-4">
          <input
            type="checkbox"
            checked={settings.workingHoursEnabled}
            onChange={(e) => onChange({ ...settings, workingHoursEnabled: e.target.checked })}
            className="w-5 h-5 rounded border-gray-300 text-blue-600"
          />
          <span className="text-gray-700">Restrict calls to working hours</span>
        </label>

        {settings.workingHoursEnabled && (
          <div className="space-y-4 pl-8">
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Start Time</label>
                <input
                  type="time"
                  value={settings.workingHoursStart}
                  onChange={(e) => onChange({ ...settings, workingHoursStart: e.target.value })}
                  className="w-full px-4 py-2 border rounded-lg"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">End Time</label>
                <input
                  type="time"
                  value={settings.workingHoursEnd}
                  onChange={(e) => onChange({ ...settings, workingHoursEnd: e.target.value })}
                  className="w-full px-4 py-2 border rounded-lg"
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Working Days</label>
              <div className="flex gap-2">
                {days.map(day => (
                  <button
                    key={day.id}
                    type="button"
                    onClick={() => toggleDay(day.id)}
                    className={`w-12 h-12 rounded-lg font-medium transition-colors ${
                      settings.workingDays?.includes(day.id)
                        ? 'bg-blue-600 text-white'
                        : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
                    }`}
                  >
                    {day.name}
                  </button>
                ))}
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Timezone</label>
              <select
                value={settings.timezone}
                onChange={(e) => onChange({ ...settings, timezone: e.target.value })}
                className="w-full px-4 py-2 border rounded-lg bg-white"
              >
                <option value="America/New_York">Eastern Time (ET)</option>
                <option value="America/Chicago">Central Time (CT)</option>
                <option value="America/Denver">Mountain Time (MT)</option>
                <option value="America/Los_Angeles">Pacific Time (PT)</option>
                <option value="UTC">UTC</option>
              </select>
            </div>
          </div>
        )}
      </div>

      {/* DNC Settings */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Do Not Call (DNC) List</h3>
        <label className="flex items-center gap-3 mb-4">
          <input
            type="checkbox"
            checked={settings.dncListEnabled}
            onChange={(e) => onChange({ ...settings, dncListEnabled: e.target.checked })}
            className="w-5 h-5 rounded border-gray-300 text-blue-600"
          />
          <span className="text-gray-700">Enable DNC list checking</span>
        </label>
      </div>

      {/* Call Attempt Limits */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Call Attempt Limits</h3>
        <div className="grid grid-cols-3 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Max per Day
            </label>
            <input
              type="number"
              value={settings.maxCallAttemptsPerDay}
              onChange={(e) => onChange({ ...settings, maxCallAttemptsPerDay: parseInt(e.target.value) || 3 })}
              className="w-full px-4 py-2 border rounded-lg"
              min="1"
              max="10"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Max Total
            </label>
            <input
              type="number"
              value={settings.maxCallAttemptsTotal}
              onChange={(e) => onChange({ ...settings, maxCallAttemptsTotal: parseInt(e.target.value) || 10 })}
              className="w-full px-4 py-2 border rounded-lg"
              min="1"
              max="50"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Min Between (mins)
            </label>
            <input
              type="number"
              value={settings.minTimeBetweenAttempts}
              onChange={(e) => onChange({ ...settings, minTimeBetweenAttempts: parseInt(e.target.value) || 60 })}
              className="w-full px-4 py-2 border rounded-lg"
              min="5"
              max="1440"
            />
          </div>
        </div>
        <p className="text-sm text-gray-500 mt-2">
          These limits help ensure compliance with calling regulations and prevent over-contacting prospects.
        </p>
      </div>
    </div>
  )
}
